import "./Headers.css"
import {Link} from "react-router-dom";
import {Button, OverlayTrigger, Tooltip} from "react-bootstrap";
import {render} from "react-dom";


const Header = () => {
    return (
        <div className="Header">
            <div className="header-left">
                <Link to="/">
                    <img className="header-logo"
                         src="data:image/png;base64,"/>
                </Link>
            </div>
            <div className={'d-flex'}>
                <div className="header-right d-none d-sm-flex">
                    <Link to="/" className="header-content">Home</Link>
                    <Link to="/contacts" className="header-content">Contacts</Link>
                    <Link to="/about" className="header-content">About</Link>
                    {/*<h4 className="header-content">Login</h4>*/}
                    <span className="material-icons">login</span>
                </div>
                <span className="menu-burger material-icons-outlined d-flex d-sm-none">
                menu
                </span>
            </div>


        </div>
    )

}


export default Header

